@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&display=swap);
main {
    padding-top: 100px;
    max-width: 1300px;
    margin: auto;
    
}

main#main-page a {
    font-size: 20rem;
    
}

main#main-page ul {
    max-width: 700px;
}

main#main-page ul div {
    background-color: transparent;
}

main#main-page ul li {
    font: bold 8rem 'Open Sans';
    color: #3d3d3d;
    text-transform: uppercase;
    letter-spacing: 0.4rem;
    transition: 0.3s;
    z-index: 40;
    background-color: transparent;
}

main#main-page ul p {
    font: 2rem 'Playfair Display';
    color: #8B8B8B;
    margin-top: -15px;
    margin-bottom: 30px;
    z-index: 4;
    background-color: transparent;
}

img {
    background-color: transparent;
    z-index: 1;
}

li {
    z-index: 3213;
    position: relative;
}

main#main-page ul li:hover {
   color: black;
   z-index: 4;
}

main#main-page .project-image {
    opacity: 0;
    transition: 0.3s;
    position: fixed;
    top: 50%;
    left: 70%;
    transform: translate(-50%, -50%);
}

main#main-page .project-image img {
    max-height: 600px;
    max-width: 600px;
    z-index: 1;
}

main#main-page a:hover + .project-image {
    opacity: 1;
}

@media (max-width: 1450px) {

    main#main-page .project-image {
        left: 85%;
    }

}


@media (max-width: 900px) {

    main#main-page .project-image {
        display: none;
    }

}

@media (max-width: 640px) {
    
    main#main-page {
        padding-bottom: 100px;
        padding-top: 120px;
    }

    main#main-page ul li {
        line-height: 7rem;
        margin-bottom: 20px;
        font-size: 4rem;
        text-align: center;
    }

    main#main-page ul p {
        padding-bottom: 30px;
        margin-bottom: 30px;
        border-bottom: 1px solid #e3e3e3;
        text-align: center;
    }
}
header#header-component {
    background-color: transparent;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
}
#menu-list-grow li {
    font-size: 1.4rem;
    color: var(--color-primary-dark);
}
button.cv-button {
    z-index: 20;
    font-size: 5rem;
    color: var(--color-primary-dark);
    border: 1px solid var(--color-primary-dark) ;
}

.cv-button .MuiButton-label {
    z-index: 100;
    font-size: 1.5rem;
    background-color: transparent;
    letter-spacing: 0.2rem;
}
.cv-button-options li .option-pdf {
    color: black;
    font-size: 1.6rem;
    z-index: 10;
    background-color: transparent;
}

.cv-button-options li .option-pdf a {
    color: var(--color-primary-dark);
    background-color: transparent;
}

@media(max-width: 800px) {
    header {
        padding: 1rem 1rem;  
    }
    header#header-component {
        background-color: #F9F9F9;
    }
   
}
.button-primary {
    padding: 0.4rem 1.2rem;
    background-color: var(--color-primary-medium);
    color: var(--color-gray-light);
    border-radius: 5px;
    border: 1px solid var(--color-primary-medium);
    font-size: 1.5rem;
    font-weight: bold;
    cursor: pointer;
    text-decoration: none;
    display: inline-flex;
    align-items: center;
    transition: 0.2s;
}

.button-primary:hover, .button-secondary:hover, .button-tertiary:hover {
    opacity: 0.8;
}

.button-secondary {
    padding: 1rem 2.2rem;
    color: var(--color-primary-dark);
    border-radius: 4px;
    border: 2px solid var(--color-primary-dark);
    font-size: 1.5rem;
    cursor: pointer;
    text-decoration: none;
    background-color: transparent;
    font-weight: 600;
    display: flex;
    align-items: center;
    line-height: 2.4rem;
    letter-spacing: 0.2rem;
}

.button-tertiary {
    color: var(--color-primary-dark);
    background-color: transparent;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    font-weight: 600;
    text-decoration: none;
    text-transform: uppercase;
}

